:root {
  --hover-link-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

header.header {
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

ul.nav__list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

a.nav__link {
  text-align: center;
  color: #000;
  padding: 22.5px 0;
  text-decoration: none;
}

a.nav__link:focus {
  outline: thin dotted;
}

a.nav__ling:active, a.nav__link:hover {
  color: #00acdc;
  outline: 0;
}

nav.nav {
  height: 60px;
  margin-right: 50px;
}

li.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-weight: 500;
  display: flex;
}

li.nav__item:last-child {
  margin-right: 0;
}

img.logo {
  padding: 10px 0 0 50px;
}

a.is-active {
  color: var(--hover-link-color);
  position: relative;
}

a.is-active:after {
  content: "";
  background: var(--hover-link-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
}
